<template lang="pug">
b-form(@submit.prevent="registration")
    b-alert(varinat="success" v-if="success" show)   !
    b-alert(variant="danger" v-if="error" show) {{ error }}
    b-form-group(
        label=" "
        )
        b-form-input(
            v-model="userData.userLogin"
            type="text"
            placeholder=" "
            )
    b-form-group(
        label=" "
        )
        b-form-input(
            v-model="userData.userPassword"
            type="password"
            placeholder=" "
            )
    b-form-group(
        label=" - "
        )
        b-form-input(
            v-model="userData.groupInvite"
            type="password"
            placeholder=" -"
            )
    b-form-group(
        label=" - "
        )
        b-form-input(
            v-model="userData.userInvite"
            type="password"
            placeholder=" -"
            )
    b-form-group(
        label="  email"
        )
        b-form-input(
            v-model="userData.userEmail"
            type="email"
            placeholder=" email"
            )
    b-form-group(
        label="  "
        )
        b-form-input(
            v-model="userData.userAuthor"
            type="text"
            placeholder=" "
            )
    b-button(type="submit") 
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    data() {
        return {
            userData: {
                userLogin: '',
                userPassword: '',
                groupInvite: '',
                userInvite: '',
                userEmail: '',
                userAuthor: '',
            },
            success: false,
            error: '',
        }
    },
    methods: {
        ...mapActions(['signUp']),
        registration(e) {
            this.signUp({ ...this.userData })
            .then(response => {
                this.success = true;
                this.error = '';
                setTimeout(() => {
                    this.$router.push('/');
                }, 1000);
            })
            .catch(err => {
                this.success = false;
                this.error = err.message;
            })
        }
    }
}
</script>



