<template lang="pug">
    b-card
        b-form-group(
            label="   "
            )
            b-btn(type="button" @click="showEditor")  

        b-modal(ref="routesEditor" title="  " hide-footer)
            b-form(@submit.prevent.stop="createNewPreset")
                b-form-group(
                    label="  "
                    )
                    b-form-input(
                        id="presetname"
                        type="text"
                        v-model="presetName"
                        required
                        placeholder="  ")
                choose-authors(
                    @updateSelectedUsers="updatePresetRoute"
                    :selectedUsers="selectedUsers"
                    )
                authors-list(:selectedUsers="selectedUsers" @updateSelectedUser="updatePresetRoute")
                b-btn(type="submit")  
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    data() {
        return {
            selectedUsers: [],
            presetName: '',
        };
    },
    methods: {
        ...mapActions('docsStore', ['createPreset']),
        showEditor() {
            this.$refs.routesEditor.show();
        },
        updatePresetRoute(selectedUsers) {
            this.selectedUsers = selectedUsers;
        },
        createNewPreset(e) {
            const newPreset = {
                title: this.presetName,
                routes: this.selectedUsers,
            }
            this.createPreset(newPreset)
            .then(response => {
                e.target.reset();
                this.selectedUsers = [];
                this.$refs.routesEditor.hide();
                this.getPresets();
            })
            console.log(newPreset);
        }
    },
    components: {
        chooseAuthors: require('./chooseAuthors'),
        authorsList: require('./authorsList'),
    },
}
</script>




