<template lang="pug">
b-form(@submit.prevent="signIn")
    b-alert(varinat="success" v-if="success" show)   !
    b-alert(variant="danger" v-if="error" show) {{ error }}
    b-form-group(
        label=" "
        )
        b-form-input(
            v-model="userLogin"
            type="text"
            placeholder=" "
        )
    b-form-group(
        label=" "
        )
        b-form-input(
            v-model="userPassword"
            type="password"
            placeholder=" "
        )
    b-button(type="submit") 
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    data() {
        return {
            userLogin: '',
            userPassword: '',
            success: false,
            error: '',
        }
    },
    methods: {
        ...mapActions('usersStore', ['getCurrentUser']),
        ...mapActions(['logIn']),
        signIn(e) {
            this.logIn({
                userLogin: this.userLogin,
                userPassword: this.userPassword,
            })
            .then(response => {
                this.success = true;
                this.error = '';
                this.getCurrentUser()
                .then(() => {
                    this.$router.push('edms');
                });
            }).catch(err => {
                this.success = false;
                this.error = err.message;
            })
        }
    }
}
</script>



